.light-theme {
    --white: white;
    --black: black;
    --secondary: #9BE9C5;
}

.dark-theme {
    --white: black;
    --black: white;
}

body {
    --black: #0f0f0f;
    --white: #f5f5f5;
    /* filter: grayscale(1); */
}

.in_gradient {
    background: linear-gradient(45deg, rgba(255, 103, 31, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(4, 106, 56, 1) 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 0.5px var(--primary);
    /* text-fill-color: transparent; */
}

.hero_title {
    font-size: 2rem;
    -webkit-text-stroke: 0.7px var(--primary);
    color: transparent;
}

*::selection {
    background-color: var(--secondary);
    color: var(--primary);
}

.rotate_50 {
    height: 79% !important;
    width: 40%;
    scale: 0.8;
    transform: rotate(-45deg);
}

.hero_bg {
    /* background: rgb(0,0,0);
    background: linear-gradient(144deg, rgba(0,0,0,1) 17%, rgba(255,255,255,1) 100%); */
    background-image: url('https://imgs.search.brave.com/TguB5f-XfP0tKVpfNd9fTqnlGvNVzW2j8v8iPoaKVUE/rs:fit:860:0:0/g:ce/aHR0cHM6Ly93YWxs/cGFwZXJjYXZlLmNv/bS93cC9FcG5NaGwx/LnBuZw');
    background-size: cover;
}

.z_imp {
    position: relative;
    z-index: 1;
}

.vignette {
    -webkit-box-shadow: inset 0px 0px 85px rgba(0, 0, 0, 0.4);
    -moz-box-shadow: inset 0px 0px 85px rgba(0, 0, 0, 0.4);
    box-shadow: inset 0px 0px 85px rgba(0, 0, 0, 0.4);
}


.shadow_anim {
    animation: shadow 3s linear infinite;
}

@keyframes shadow {

    0%,
    100% {
        box-shadow: inset 0px -5px 2px black;
    }

    25% {
        box-shadow: inset 5px 0px 2px black;
    }

    50% {
        box-shadow: inset 0px 5px 2px black;
    }

    75% {
        box-shadow: inset -5px 0px 2px black;
    }
}

.xvy {
    box-shadow: -5px 0px 5px black;
}

