.ThemePopup input {
    width: 100%;
    height: 20px;
    background: none;
    padding: 0;
    margin: 0;
    border: 0;
    outline: 0;
}

.scrlbar::-webkit-scrollbar {
    background-color: var(--secondary);
    width: 7px;
    height: 10px;
    border-radius: 20px;
}

.scrlbar::-webkit-scrollbar-thumb {
    background-color: var(--primary);
    border-radius: 20px;
}